hite-font-color);\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n.slogan {\r\n    font-size: 40px;\r\n    font-family: Anton;\r\n}\r\n\r\n.btn-container {\r\n    margin-top: 20px;\r\n    display: flex;\r\n    gap: 4px;\r\n}\r\n\r\n.btn-container button {\r\n    width: 150px;\r\n    padding: 10px;\r\n    color: var(--white-font-color);\r\n    font-weight: bold;\r\n    border: none;\r\n    border-radius: 4px;\r\n}\r\n\r\n.red-btn {\r\n    background-color: #e02d00;\r\n}\r\n\r\n.red-btn:hover {\r\n    background-color: #f55e38;\r\n    cursor: pointer;\r\n}\r\n\r\n.black-btn {\r\n    background-color: black;\r\n}\r\n\r\n.black-btn:hover {\r\n    background-color: rgb(58, 57, 57);\r\n    cursor: pointer;\r\n}\r\n\r\n/* Menu page */\r\n\r\n.menu-content {\r\n    height: 100%;\r\n    width: 70%;\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 40px;\r\n    margin-left: 40px;\r\n    margin-top: 30px;\r\n}\r\n\r\n.food-container {\r\n    background-color: var(--white-font-food-color);\r\n    padding: 40px;\r\n    color: var(--main-background-dark);\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 5px;\r\n    justify-content: center;\r\n    align-items: center;\r\n    border-radius: 40px;\r\n    box-shadow: 3px 3px 5px black;\r\n    cursor: pointer;\r\n    transition: transform .2s;\r\n}\r\n\r\n.food-container:hover {\r\n    transform: scale(1.1);\r\n}\r\n\r\n.food-container div {\r\n    font-family: 'Tangerine', Anton, Tiltprism;\r\n}\r\n\r\n\r\n/* Contacts page */\r\n\r\n.contacts-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 20px;\r\n    width: 70%;\r\n}\r\n\r\n.contacts-container div {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    height: 100px;\r\n    width: 90%;\r\n    gap: 10px;\r\n    font-size: 50px;\r\n    font-family: Anton;\r\n    color: var(--white-font-color);\r\n    background-color: #2f353d85;\r\n    border-radius: 3px;\r\n    padding: 10px;\r\n}\r\n\r\n.contacts-container div i {\r\n    font-size: 50px;\r\n}\r\n\r\n.fa-github {\r\n    color: var(--github-color);\r\n}\r\n\r\n.fa-twitter {\r\n    color: var(--twitter-color);\r\n}\r\n\r\n.fa-linkedin {\r\n    color: var(--linkedin-color);\r\n}\r\n\r\n/* Footer */\r\n\r\n.footer {\r\n    background-color: var(--main-background-dark);\r\n    display: flex;\r\n    gap: 10px;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.fa-github {\r\n    font-size: 20px;\r\n}\r\n\r\n \r\n@media only screen and (max-width: 700px) {\r\n    body {\r\n        height: 105vh;\r\n        width: 100vw;\r\n    }\r\n\r\n    img {\r\n        width: 150px;\r\n        height: 150px;\r\n    }\r\n\r\n    .pages {\r\n        width: 100%;\r\n        height: 100%;\r\n    }\r\n\r\n    #content {\r\n        display: flex;\r\n        flex-direction: column;\r\n        justify-content: center;\r\n        align-items: center;\r\n    }\r\n\r\n    .side-bar {\r\n        width: 100vw;\r\n        height: 200px;\r\n    }\r\n\r\n    .side-bar-content {\r\n        display: flex;\r\n        flex-direction: column;\r\n        width: 100%;\r\n        height: 100%;\r\n    }\r\n\r\n    .logo-container {\r\n        height: 200px;\r\n    }\r\n\r\n    .tabs-container {\r\n        padding: 5px;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n    }\r\n\r\n    .tabs-list {\r\n        display: flex;\r\n        flex-direction: row;\r\n        gap: 15px;\r\n    }\r\n\r\n    .tabs-list li {\r\n        font-size: 15px;\r\n    }\r\n\r\n    .tabs-list div {\r\n        gap: 0;\r\n        padding: 0 5px 0 5px;\r\n    }\r\n\r\n    .tabs-list i {\r\n        font-size: 15px;\r\n    }\r\n\r\n    /* Home page */\r\n\r\n    .home-content {\r\n        gap: 30px;\r\n        padding: 50px 0 0 0;\r\n    }\r\n\r\n    .welcome-to {\r\n        font-size: 40px;\r\n    }\r\n\r\n    .restaurant-name {\r\n        text-align: center;\r\n        font-size: 50px;\r\n    }\r\n\r\n    .slogan {\r\n        font-size: 20px;\r\n        text-align: center;\r\n    }\r\n\r\n    .btn-container {\r\n        gap: 5px;\r\n    }\r\n\r\n    .btn-container button {\r\n        width: 125px;\r\n        padding: 5px;\r\n    }\r\n\r\n\r\n    /* Contacts page */\r\n\r\n    .contacts-container div {\r\n        font-size: 20px;\r\n    }\r\n\r\n    .contacts-container div i {\r\n        font-size: 40px;\r\n    }\r\n\r\n    /* Footer */\r\n\r\n    .footer {\r\n        width: 100%;\r\n        padding: 15px 0 15px 0;\r\n    }\r\n\r\n    .menu-content {\r\n        display: flex;\r\n        flex-direction: column;\r\n        gap: 20px;\r\n        margin: 0;\r\n    }\r\n}"],"sourceRoot":""}]);
